import * as React from "react"
import { GatsbyImage } from "gatsby-plugin-image"

const TileThreeColumn = props => {
  return (
    <section className="page-section cta">
      <div className="container">
        <div className="cta-inner bg-faded p-0 pt-4">
          <div className="text-center rounded mb-5 mt-0 ms-5 me-5 p-2">
            <h2 className="section-heading mb-0">
              <span className="section-heading-upper">{props.subtitle}</span>
              <span className="section-heading-lower">{props.title}</span>
            </h2>
          </div>
          <div className="row text-center align-items-start p-3">
            <div className="col-xl-3 ms-auto">
              <GatsbyImage
                className="intro-img img-fluid mb-3 rounded custom-box-shadow"
                image={props.tileOneImage}
                quality={95}
                placeholder="blurred"
                formats={["auto", "webp", "avif"]}
                alt="wood pool image"
              />
              <p>{props.tileOneDescription}</p>
            </div>
            <div className="col-xl-3 mx-auto">
              <GatsbyImage
                className="intro-img img-fluid mb-3 rounded custom-box-shadow"
                image={props.tileTwoImage}
                quality={95}
                placeholder="blurred"
                formats={["auto", "webp", "avif"]}
                alt="wood pool image"
              />
              <p>{props.tileTwoDescription}</p>
            </div>
            <div className="col-xl-3 me-auto">
              <GatsbyImage
                className="intro-img img-fluid mb-3 rounded custom-box-shadow"
                image={props.tileThreeImage}
                quality={95}
                placeholder="blurred"
                formats={["auto", "webp", "avif"]}
                alt="wood pool image"
              />
              <p>{props.tileThreeDescription}</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default TileThreeColumn
